.contacts {
}
.contacts iframe {
  width: 100%;
  height: 100%;
  border: none;
}
.contacts-right {
  padding: 50px;
}
.contacts-title {
  font-size: 30px;
  font-weight: 400;
}
.contacts p {
  margin: 30px 0;
  color: rgb(75, 74, 74);
}
.contacts .items {
  margin: 30px 0;
}

.contacts .box p {
  font-size: 17px;
  margin-top: 10px;
}
.contacts .box a {
  display: inline-block;
  font-size: 17px;
  margin-top: 10px;
  color: #3fb4e6;
}
.contacts .box a:hover {
  color: #055d82;
}

.contacts textarea,
.contacts input {
  width: 100%;
  padding: 15px;
  margin-bottom: 15px;
  outline: none;
  border: 1px solid grey;
}
.contacts .flexSB input:nth-child(1) {
  margin-right: 10px;
}
.contacts h3 {
  margin-top: 20px;
  font-weight: 500;
  margin-bottom: 10px;
}
.contacts span {
  color: #3fb4e6;
  font-weight: 600;
  font-size: 15px;
  word-spacing: 5px;
}
.center-button {
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 768px) {
  .contacts .container {
    flex-direction: column;
  }
  .contacts iframe {
    height: 50vh;
  }
  .contacts .row {
    width: 100%;
  }
  .contacts .items {
    margin: 15px 0;
  }
  .contacts .box p {
    margin-top: 0px;
    margin-bottom: 0px;
  }
  .contacts-right {
    padding: 10px;
  }
  form .flexSB {
    display: block;
  }
}
